@use 'base/variables'as v;
@use 'base/mixins' as m;

.wrapper_editar-publicacion {
    padding: 2rem;
    @include m.tablet {
        display: flex;
        align-items: center;
    }

    .post {
        background-color: v.$blanco;
        padding: 1.5rem;
        display: flex;
        flex-direction: column;
        row-gap: 3rem;

        @include m.tablet {
            height: fit-content;
        }

        .post-header {
            display: flex;
            justify-content: center;
            row-gap: 2rem;

            @include m.tablet {
                align-items: center;
            }

            h2.autor-post {
                font-size: 3rem;
                text-align: start;
                color: v.$azul;
                font-weight: 700;
            }

            img.img-usuario {
                border-radius: 50%;
                width: 5rem;
            }
        }

        .post-body {
            text-align: start;

            h3.titulo {
                font-weight: 700;
                font-size: 2rem;
                color: v.$negro;
            }

            p.cuerpo {
                color: v.$negro;
            }

            img.imagen-post {
                width: 100%;
            }
        }

        p.fecha {
            text-align: end;
            color: v.$rojoLabial;
            font-size: 1.3rem;
        }
    }

    .post_publicacion {
        padding: 1.5rem;
        margin-top: 3rem;
        margin-bottom: 5rem;

        .form {

            legend {
                font-size: 4rem;
                color: v.$blanco;
            }

            .campo {
                padding: 1rem;
                display: flex;
                flex-direction: column;
                row-gap: 1rem;

                label {
                    font-size: 2rem;
                    color: v.$blanco;
                }

                input {
                    font-size: 2rem;
                    color: v.$negro;
                    outline: none;
                }

                textarea {
                    font-size: 2rem;
                    color: v.$negro;
                    outline: none;
                }
            }

            .btn-area {
                display: flex;
                flex-direction: column;
                padding: 1rem;

                input.boton {
                    background-color: v.$azul;
                    color: v.$blanco;
                    text-transform: uppercase;
                    padding: 1rem;
                    font-size: 1.5rem;
                    margin-top: 1rem;
                    font-weight: v.$bold;
                    border: none;
                    outline: none;
                    cursor: pointer;
                }

                .custom-file-upload {
                    display: flex;
                    justify-content: center;
                    column-gap: 1rem;
                    padding: 1rem;
                    align-items: center;
                    width: 100%;
                    background-color: v.$azul;
                    color: v.$blanco;
                    font-weight: 600;
                    text-transform: uppercase;
                    text-align: center;
                    cursor: pointer;

                    p {
                        font-size: 1.5rem;
                        margin: 0;
                        font-weight: 700;
                    }

                    i {
                        font-size: 2rem;
                    }
                }
            }
        }
    }
}
