@use 'base/variables' as v;
@use 'base/mixins' as m;

.post-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;

    h2.account-post-title {
        font-size: 2rem;
        margin-bottom: 2rem;
    }

    .contenedor-posts {

        .form-post {
            background-color: v.$blanco;
            @include m.tablet {
                width: 30rem;
            }

            .post {
                display: flex;
                flex-direction: column;
                justify-content: center;

                .post-header {
                    padding: 1rem;
                    column-gap: 1rem;
                    display: flex;
                    justify-content: left;
                    align-items: center;
                    
                    img.img-usuario {
                        width: 5rem;
                        border-radius: 50%;
                    }

                    h2.autor-post {
                        font-size: 2rem;
                        color: v.$azul;
                        font-weight: 700;

                    }
                }

                .post-body {
                    padding: 1rem;
                    text-align: left;

                    h3.titulo {
                        text-align: left;
                        font-weight: 700;
                        color: v.$negro;
                    }

                    p.cuerpo {
                        color: v.$negro;
                    }
                }

                p.fecha {
                    font-size: 1.4rem;
                    color: v.$rojoLabial;
                    text-align: end;
                }
            }

            
            .acciones {
                padding: 1rem;
                column-gap: 1rem;
                justify-content: space-around;
                display: flex;

                input.boton {
                    width: 15rem;
                    text-transform: uppercase;
                    text-align: center;
                    background-color: v.$azul;
                    color: v.$blanco;
                    padding: 1rem;
                    font-size: 1.5rem;
                    border: none;
                    outline: none;
                    cursor: pointer;
    
                    @include m.tablet {
                        width: 12rem;
                        font-size: 1.1rem;
                    }
                }

                input.boton-borrar {
                    width: 15rem;
                    text-transform: uppercase;
                    text-align: center;
                    background-color: v.$rojoLabial;
                    color: v.$blanco;
                    padding: 1rem;
                    font-size: 1.5rem;
                    border: none;
                    outline: none;
                    cursor: pointer;
    
                    @include m.tablet {
                        width: 12rem;
                        font-size: 1.2rem;
                    }
                }
            }
        }
    }
}