@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    width: 100%;

    .field {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        flex-direction: column;
        justify-content: center;

        @include m.tablet {
            flex-direction: row;
        }


        label {
            font-size: 1.5rem;
            margin-bottom: 1rem;
            color: v.$blanco;

            @include m.tablet {
                margin-right: 2rem; 
                margin-bottom: 0;
            }
        }

        input {
            padding: calc(v.$separacion/4);
            width: 100%;
            border: none;
            outline: none;
            
        }
    }

    .boton {
        background-color: v.$azul;
        color: v.$blanco;
        text-transform: uppercase;
        padding: 1rem;
        font-size: 1.5rem;
        margin-top: 2rem;
        font-weight: v.$bold;
        cursor: pointer;
    }
}