@use 'base/variables' as v;

.main_layout {
    min-height: 100vh;
    background-color: gray;

    .app {
        margin-bottom: 5rem;

        .navbar {
            padding: 1rem;

            a.navbar-brand {
                font-size: 2rem;
            }

            a.nav-link {
                font-size: 2rem;

                img.imagen-post {
                    width: 5rem;
                    height: 4rem;
                    border-radius: 5rem;
                }
            }
        }

        .post_publicacion {
            padding: 1rem;

            .form {
                width: 100%;

                legend {
                    font-size: 2rem;
                    color: v.$blanco;
                    margin-bottom: 1rem;
                }
    
                .campo {
                    margin-bottom: 1rem;
    
                    label {
                        margin-bottom: 1rem;
                        font-size: 1.5rem;
                        color: v.$blanco;
                    }

                    input {
                        padding: 0.5rem;
                        font-size: 1.5rem;
                        outline: none;
                    }

                    textarea {
                        font-size: 1.5rem;
                        padding: 0.5rem;
                        outline: none;
                    }
                }
    
                input.boton {
                    background-color: v.$azul;
                    color: v.$blanco;
                    text-transform: uppercase;
                    padding: 1rem;
                    font-size: 1.5rem;
                    margin-top: 1rem;
                    font-weight: v.$bold;
                    border: none;
                    outline: none;
                    cursor: pointer;
                }

                .custom-file-upload {
                    padding: 0.2rem;
                    width: 15rem;
                    height: 5rem;
                    background-color: v.$azul;
                    color: v.$blanco;
                    font-weight: 600;
                    text-transform: uppercase;
                    text-align: center;
                    cursor: pointer;

                    i {
                        font-size: 4rem;
                    }
                }
            }
        }

        .post_publicacion_otros {
            padding: 1rem;
            max-height: 40rem;
            overflow: auto;
            
            .contenedor-posts {
                row-gap: 2rem;

                .post {
                    padding: 1rem;
                    row-gap: 1.5rem;
                    background-color: v.$blanco;

                    h2.autor {
                        color: v.$azul;
                        font-weight: 600;
                        font-size: 2.5em;
                    }
                    

                    h3.titulo {
                        color: v.$negro;
                        font-size: 2rem;
                        margin-bottom: 1.5rem;
                    }

                    p.cuerpo {
                        color: v.$negro;
                        text-align: center;
                        font-size: 1.9rem;
                    }

                    p.fecha {
                        color: v.$rojoLabial;
                        font-size: 1.2rem;
                        text-align: end;
                    }

                    img.imagen-post {
                        width: 100%;
                        height: 30rem;
                    }

                    img.img-usuario {
                        width: 5rem;
                        height: 100%;
                        border-radius: 5rem;
                    }
                }
            }
        }

        .post_bienvido_container {
            background-image: url(../img/background-user.webp);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 2rem;
            min-height: 15rem;

            h1.post_bienvenido {
                color: black;
            }
        }

        }



}