@use 'base/variables'as v;
@use 'base/mixins' as m;

.field-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background-color: v.$gris;
    height: 100vh;
    margin-bottom: 5rem;

    h1.field-wrapper-title {
        font-size: 3rem;
        color: v.$negro;
    }

    form.form-cuenta {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .field-cuenta {
            display: flex;
            flex-direction: column;
            justify-content: center;

            .field-picture {
                 display: flex;
                 flex-direction: column;
                 justify-content: center;
                 align-items: center;
                 margin-top: 2rem;

                img.imagen-post {
                    border-radius: 50%;
                    width: 10rem;
                }

                p {
                    font-size: 1.5rem;
                    color: v.$negro;
                }
            }

            .field {
                padding: 1rem;
                display: grid;
                align-items: center;
                grid-template-columns: 30% 60% 15%;
                
                label.form-label {
                   font-size: 1.5rem;
                }

                img.edit-img  {
                    margin-left: 1rem;
                    width: 4rem;
                }
             }
        }

        .boton-area-container {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            row-gap: 2rem;
            padding: 1rem;
            width: 100%;

            @include m.tablet {
                width: 50rem;
                flex-direction: row;
                justify-content: space-evenly;
                margin-bottom: 5rem;
            }
            
            a {
                text-decoration: none;
            }

            input.boton {
                width: 15rem;
                text-transform: uppercase;
                text-align: center;
                background-color: v.$azul;
                color: v.$blanco;
                padding: 1rem;
                font-size: 1.5rem;
                border: none;
                outline: none;
                cursor: pointer;

                @include m.tablet {
                    width: 12rem;
                    font-size: 1.1rem;
                }
            }

            a.boton {
                width: 15rem;
                text-transform: uppercase;
                text-align: center;
                background-color: v.$azul;
                color: v.$blanco;
                padding: 1rem;
                font-size: 1.5rem;
                border: none;
                outline: none;
                cursor: pointer;

                @include m.tablet {
                    width: 12rem;
                    font-size: 1.2rem;
                }
            }

            a.boton-borrar {
                width: 15rem;
                text-transform: uppercase;
                text-align: center;
                background-color: v.$rojoLabial;
                color: v.$blanco;
                padding: 1rem;
                font-size: 1.5rem;
                border: none;
                outline: none;
                cursor: pointer;

                @include m.tablet {
                    width: 12rem;
                    font-size: 1.2rem;
                }
            }
        }
    }
}