@use 'base/variables' as v;

.wrraper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../img/background.webp');
    background-size: cover;
    background-position: center center;
    min-height: 100vh;

    .content {
        background-color: v.$negro;
        text-align: center;
        padding: 3rem;
        width: 50rem;
        margin: 3rem;

        .titulo-pagina {
            color: v.$blanco;
        }

        .parrafo-pagina {
           margin-top: 2rem;
           margin-bottom: 2rem;
        }
    }

    .form-links {
        margin-top: 4rem;
        display: flex;
        text-align: center;
        justify-content: space-around;
        
        .form-link {
            text-decoration: none;
            color: v.$blanco;
            font-size: 1.5rem;
        }
    }
}
