@use 'base/variables' as v;
@use 'base/mixins' as m;

.wrapper-borrar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    
    @include m.tablet {
       align-items: center;
    }

    h1 {
       font-size: 3rem;
    }

    .form {
        width: 100%;

        @include m.tablet {
            width: 30rem;
        }

        legend {
            text-align: center;
            color: v.$blanco;
            margin-top: 2rem;
            margin-bottom: 2rem;
        }

        .campo {
            padding: 1rem;
            display: flex;
            flex-direction: column;
            row-gap: 2rem;
    
            input.validacion {
                outline: none;
                font-size: 1.5rem;
                padding: 1rem;
            }

            input.boton-eliminar {
                padding: 1rem;
                color: v.$blanco;
                background-color: v.$rojoLabial;
                text-transform: uppercase;
                outline: none;
                border: none;
                font-weight: 700;
            }

            .btn-deshabilitar {
                cursor: not-allowed;
                opacity: 0.4;
            }
        }
    }
}